import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject, map } from 'rxjs';
import { IStockInicial } from '../models/stock-inicial';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';
@Injectable()
export class StockInicialService {
    constructor(private http: HttpClient, private _authService: AuthService) { }

    /**
     * Funcion en el servicio
     * - Esta funcion retorna los sotcks iniciales.
     * @returns sotcks iniciales
     */
    getStockInicial(sort?: any): Observable<Response[]> {
        let f = '?expand=usuario,deposito,stkMovimientos.idarticulo0,stkMovimientos.idunidadmedida0&';
        f += sort?.filter?.sort == 'desc' ? 'sort=fecha_hora&' : 'sort=-fecha_hora&';
        f += sort?.filter?.deposito ? `filter[iddeposito]=${sort.filter.deposito}&` : ''
        f += sort?.filter?.usuario ? `filter[idusuario]=${sort.filter.usuario}&` : ''
        f += sort?.paginado?.pagination ? '&pagination=1&' : '';
        f += sort?.paginado?.perpage ? 'per-page=' + sort.paginado.perpage + '&' : '';
        f += sort?.paginado?.currentPage ? 'page=' + sort.paginado.currentPage + '&' : '';

        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.stock_inicial}${f}`, { observe: 'response' })
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        'list': resp.result,
                        'totalPages': resp.headers.get('x-pagination-page-count'),
                        'totalCount': resp.headers.get('x-pagination-total-count')
                    })
            });
        return subject.asObservable();
    }

    getItems(idstockinicial?: number) {
        let f = `?filter[idstockinicial]=${idstockinicial}`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_movimientos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getArticulos(idarticulo?: number, filterLike?: string) {
        let f = `?filter[activo]=1&pagination=1&per-page=10&page=1`;
        if (idarticulo) {
            f += `&filter[idarticulo]=${idarticulo}`;
        } else if (filterLike) {
            f += `&filter[or][0][codigo_interno][like]=${filterLike}&filter[or][1][descripcion][like]=${filterLike}`;
        }
        
        let subject = new Subject<any>();
        this.http.get<any>(`${environment.stock.articulos}${f}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        list: resp.result
                    })
            });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUsuarios() {
        let f = `?sort=asc`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param stock_inicial 
     * @returns Crea un item nuevo
    */
    saveStockInicial(stock_inicial: IStockInicial): Observable<IStockInicial> {
        return this.http.post<IStockInicial>(`${environment.stock.stock_inicial}`, stock_inicial);
    }

    /**
     * Funcion en el servicio
     * @param stock_inicial
     * @returns Item actualizado actualizado
    */
    updateStockInicial(stock_inicial: IStockInicial, idstockinicial: number): Observable<IStockInicial> {
        return this.http.put<IStockInicial>(`${environment.stock.stock_inicial}` + '/' + idstockinicial, stock_inicial);
    }

    /**
     * Funcion en el servicio
     * @param idstockinicial 
     * @returns Elimina un item
    */
    deleteStockInicial(idstockinicial: number): Observable<IStockInicial> {
        return this.http.delete<IStockInicial>(environment.stock.stock_inicial + '/' + idstockinicial);
    }


}